@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;600&display=swap);
body {
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
}

* {
  margin: 0;
  padding: 0;
  font-family: "Source Sans Pro" sans-serif;
}

body {
  background-color: #f1f5f6;
  background-color: var(--clr-bg-gray);
}

a,
span {
  margin: 0;
  padding: 0;
}

a {
  cursor: pointer;
}

:root {
  --clr-black: #1f2d3d;
  --clr-bg-gray: #f1f5f6;
  --clr-line-gray: #eeeaea;
  --clr-info-gray: #8492a6;
  --clr-blue-main: #163c80;
  --clr-blue-tag: #1f75e1;
  --clr-sec-gray: #f8f7f7;
}

a,
h1,
h2,
h3,
h4,
p {
  color: #1f2d3d;
  color: var(--clr-black);
  text-decoration: none;
}

a:hover {
  color: #1f2d3d;
  color: var(--clr-black);
}

.main-content {
  padding-top: 55px;
  background-color: #f1f5f6;
  background-color: var(--clr-bg-gray);
  max-width: 450px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0 auto;
}

.main-title {
  display: flex;
  background-color: #163c80;
  background-color: var(--clr-blue-main);
  flex-direction: column;
  justify-content: center;
  position: relative;
  padding: 40px 15px;
}

.row-title {
  display: flex;
  flex-direction: column;
  text-align: center;
  line-height: 1.5;
}

.row-title h1 {
  font-size: 18px;
  color: white;
  font-weight: 600;
}

.row-title p {
  color: white;
  font-size: 13px;
}

.divide {
  margin: 20px 0;
  height: 1px;
}

.swiper-pagination-bullet {
  background: transparent !important;
  border: 2px solid white;
  opacity: 1 !important;
}

.swiper-button-next,
.swiper-button-prev {
  background: white;
  border-radius: 50%;
  color: #163c80 !important;
  color: var(--clr-blue-main) !important;
  height: 20px !important;
  width: 20px !important;
}

.swiper-button-next::after,
.swiper-button-prev::after {
  font-size: 12px !important;
  font-weight: 600 !important;
}

.swiper-pagination-bullet-active {
  background: white !important;
  border: 1px solid white;
}

.image-banner {
  max-width: 450px;
  display: flex;
  justify-content: center;
}

.image-banner img {
  width: 100%;
}

.header{display:flex;width:100%;position:fixed;z-index:100;background-color:white;box-shadow:0 1px 8px #7a7a7a;align-items:center;justify-content:space-between;color:var(--clr-black)}.header .header-logo{display:flex;padding:9px 12px 10px;cursor:pointer;align-items:center}.header .header-logo :hover{color:var(--clr-blue-main)}.header-logo-img{height:36px;margin-right:6px}.header-logo-img img{height:100%}.header-text{font-size:11px}.header-burger{display:flex;margin-right:0 25 px;width:37px;height:37px;background-color:var(--clr-blue-main);border-radius:50%;justify-content:center;align-items:center;cursor:pointer}.strip-wrapper{display:flex;flex-direction:column;grid-gap:0.2rem;gap:0.2rem}.strip-wrapper .strip{height:2px;width:20px;background-color:white}

.footer{text-align:center;bottom:0;background-color:white;padding:15px 32px;z-index:-1}.footer__nav{border-bottom:1px solid var(--clr-line-gray);padding:32px 0px 26px;font-weight:600}.footer__nav a{font-size:14px;font-weight:normal;margin:0 10px}.footer__nav a :hover{color:var(--clr-blue-main)}.footer__address{padding:32px 0px 26px;border-bottom:1px solid var(--clr-line-gray);line-height:1.5}.footer__address h1{font-size:16px;margin-bottom:5px}.footer__address p span{font-weight:600}.footer__address__button{display:flex;width:100%;flex-direction:column;justify-content:center}.footer__address .maps,.footer__address .phone{display:flex;width:200px;margin:10px auto;padding:10px 15px;align-items:center;justify-content:center;text-align:center}.footer__address .maps img,.footer__address .phone img{max-width:100%}.footer__address .maps span,.footer__address .phone span{font-size:14px;margin-right:5px}.footer__address .maps{border:2px solid var(--clr-blue-main);background-color:white;color:var(--clr-blue-main);font-weight:600;cursor:pointer;border-radius:2px;align-items:center}.footer__address .maps:hover{background-color:var(--clr-blue-main);color:white;transition:0.5s}.footer__address .phone{background-color:var(--clr-blue-main);color:white;border-radius:2px;border:none;font-weight:600;cursor:pointer}.footer__text{display:flex;flex-direction:column;margin-bottom:25px;justify-content:center;text-align:center}.footer__text a{margin:15px 0}.footer__text h3{padding:25px 0 15px}.copyright{background-color:var(--clr-blue-main);width:100%;text-align:center;padding:22px 0 125px}.copyright p{color:white}

.floating-wrapper{z-index:100;display:flex;justify-content:center;position:fixed;bottom:5px;left:0;right:0;padding:25px}.floating-wrapper .floating-item-wrapper{background-color:white;display:flex;margin:0 auto;text-align:center;flex-direction:row;border-radius:10px;width:100%;padding:15px;box-shadow:0px 4px 4px rgba(0,0,0,0.05)}.floating-wrapper .floating-button{width:25%}.floating-wrapper .floating-button span{font-size:10px}.floating-wrapper .floating-image{width:36px;height:36px;margin:0 auto;border-radius:50%;overflow:hidden}.floating-wrapper .floating-image img{width:100%}.floating-wrapper .booking-button{display:flex;color:white;justify-content:center;text-align:center;cursor:pointer;padding:12px 21px;width:50%;align-items:center;background-color:var(--clr-blue-main);border-radius:2px}

.hide{display:none}

.modal-cover{position:fixed;top:0;left:0;width:100%;height:100%;z-index:10;-webkit-transform:translateZ(0);transform:translateZ(0);background-color:rgba(0,0,0,0.8)}.modal-area{position:fixed;top:0;left:0;width:100%;height:100%;padding:2.5em 1.5em 1.5em 1.5em;background-color:#ffffff;box-shadow:0 0 10px 3px rgba(0,0,0,0.1);overflow-y:auto;-webkit-overflow-scrolling:touch}._modal-close{position:absolute;top:0;right:0;padding:0.5em;line-height:1;background:#f6f6f7;border:0;box-shadow:0;cursor:pointer}._modal-close-icon{width:25px;height:25px;fill:transparent;stroke:black;stroke-linecap:round;stroke-width:2}._hide-visual{border:0 !important;clip:rect(0 0 0 0) !important;height:1px !important;margin:-1px !important;overflow:hidden !important;padding:0 !important;position:absolute !important;width:1px !important;white-space:nowrap !important}form{border-radius:8px;box-shadow:0 4px 4px rgba 0,0,0,0.1}

table{width:100%}td{text-align:left;margin-right:5px;font-size:14px}.right{font-weight:600}

.card-container{max-width:100%;position:relative;padding:0 15px;margin-top:-100px}.content{display:flex;flex-direction:column;width:100%;height:auto;margin-bottom:25px;box-shadow:0px 4px 8px rgba(165,165,165,0.12);border-radius:8px;overflow:hidden}.content__img{width:100%;border-radius:8px 8px 0 0;overflow:hidden}.content__img img{width:100%;height:100%}.content :hover h1:hover{color:var(--clr-blue-main) !important}.content__text{background-color:white;margin:0;padding:15px 15px 21px}.content__text .card-tag{display:inline-block;background-color:rgba(31,117,225,0.08);font-size:11px;padding:5px 8px;border:1px solid var(--clr-blue-tag);border-radius:2px}.content__text .card-tag p{color:var(--clr-blue-tag)}.content__text .card-outletName{margin:11px 0 9px}.content__text .card-outletInfo{align-items:center;display:flex;margin:5px 0}.content__text .card-outletInfo span{margin-right:6px}.content__text .card-outletInfo p{font-size:12px;font-weight:600;color:var(--clr-info-gray)}.content .outlet-btn{width:100%;text-align:center;padding:17px 0;background-color:white;border:2px solid var(--clr-blue-main);color:var(--clr-blue-main);font-weight:600;border-radius:2px;cursor:pointer}.content .outlet-btn:hover{background-color:var(--clr-blue-main);color:white;transition:0.2s}

.tab-nav{display:flex;text-align:center;background-color:var(--clr-blue-main);margin-top:-10px}.tab-nav .nav{flex:0 0 1;width:100%;padding:10px;opacity:0.5}.tab-nav .active{opacity:1;border-bottom:4px solid white}.tab-nav a{color:white;font-weight:600}.tab-nav a:hover{color:var(--clr-blue-main);transition:0.3s}.car-list{padding:30px 16px;background-color:white;-webkit-animation:fadein 2s;animation:fadein 2s}@keyframes fadein{from{opacity:0}to{opacity:1}}@-webkit-keyframes fadein{from{opacity:0}to{opacity:1}}.car-list-hide{display:none}.car-item{display:flex;padding:24px 0 26px;align-items:center;border-bottom:1px solid var(--clr-line-gray)}.item-image,.item-desc{width:100%;flex:0 0 1}.item-image{margin-right:10px}.item-image img{width:100%}.item-desc p{padding:4px 0;font-size:12px;color:var(--clr-info-gray);font-style:italic}.item-desc h5{font-size:14px;color:var(--clr-blue-main);margin-bottom:5px}.item-desc .car-tag{display:inline-block;background-color:rgba(31,117,225,0.08);font-size:11px;padding:2px 8px;border:1px solid var(--clr-blue-tag);border-radius:2px}.item-desc .car-tag p{color:var(--clr-blue-tag);font-style:normal}.catalog-btn{text-align:center;margin:28px auto;padding:0 auto}.catalog-btn a{padding:14px 20px;border:2px solid var(--clr-blue-main);border-radius:2px;color:var(--clr-blue-main);font-weight:600}.catalog-btn a:hover{color:white;background-color:var(--clr-blue-main);transition:0.3s}

.nav-content{margin-top:55px;position:fixed;width:100%;z-index:100;background-color:var(--clr-sec-gray)}.car-name{padding:15px;font-size:13px;font-weight:600;border-bottom:1px dotted var(--clr-line-gray)}.nav-tab{width:auto;text-align:center;font-size:12px;font-weight:600;display:flex;flex-wrap:nowrap;align-items:center;overflow-x:scroll}.nav-tab .nav-item{width:100%;display:block;align-items:center}.nav-tab .nav-item a{display:flex;flex-wrap:nowrap;white-space:nowrap;width:auto;padding:5px 12px}.nav-tab::-webkit-scrollbar{display:none}.motto{width:94%;margin:0 auto}.motto img{width:100%;height:auto}.container-btn-title{padding:25px;background-color:var(--clr-blue-main);border-top:1px solid rgba(255,255,255,0.2);display:flex;margin-top:10px;justify-content:center;align-items:center}.container-btn-title .brosur,.container-btn-title .harga{padding:10px 16px;text-align:center;font-weight:600;margin:0 5px;border:2px solid white}.container-btn-title .brosur{color:white;border-radius:2px}.container-btn-title .harga{background-color:white;color:var(--clr-blue-main);border-radius:2px}.container{margin:12.5px 0;background-color:white;padding:25px;text-align:center}.container .title{font-weight:600;margin-bottom:30px}.container .nav-type{display:flex;justify-content:center;width:100%;padding:5px 0}.container .nav-type .color{width:37px;height:37px;border-radius:50%;border:2px solid white;box-shadow:0px 4px 8px rgba(61,61,61,0.12);margin:0 10px;cursor:pointer}.container .nav-type .nav-item{padding:8px 16px;color:rgba(17,16,16,0.3);border-bottom:3px solid rgba(17,16,16,0.3)}.container .nav-type .active{color:var(--clr-blue-main);border-color:var(--clr-blue-main)}.container .type-color-content{padding:10px 15px}.container .type-color-content .car-image{padding:15px 0;-webkit-animation:fadein 1s;animation:fadein 1s}.container .type-color-content .car-image img{width:100%}.container .type-color-content .color-name{font-weight:600}.container .type-color-content .hide{display:none}.container .pricing-container{background-color:var(--clr-sec-gray);cursor:pointer;border-radius:8px;text-align:left;display:flex;padding:15px;margin:16px 0;box-shadow:0px 4px 8px rgba(61,61,61,0.12)}.container .pricing-container .card-name,.container .pricing-container .card-price{width:100%;flex:0 0 50%;font-weight:600}.pricing-btn{margin:15px auto;display:inline-block;padding:13px 20px;color:var(--clr-blue-main);border-radius:2px;border:2px solid var(--clr-blue-main);font-weight:600}.exterior{text-align:center;width:100%;margin:12.5px 0}.exterior .title{width:100%;padding:25px 0;background-color:var(--clr-blue-main)}.exterior .title h4{color:white}.exterior .title .interior-btn{border:2px solid white;border-radius:2px;display:inline-block;padding:10px 20px;color:white;background-color:transparent}.exterior .detail{width:100%;height:auto}.exterior .detail img{width:100%;height:100%}.scroll-lock{overflow:hidden;margin-right:17px}

